	onLoad() {
		//获取手机系统信息
		uni.getSystemInfo({
			success:(res)=>{
				//获取可使用屏幕高度 再减去 顶部选项卡的高度，再调用方法转为upx
				let height = res.windowHeight - uni.upx2px(100);
				this.swiperHeight = height;
			}
		})
	},
	//监听原生搜索框点击事件
	onNavigationBarSearchInputClicked() {
		uni.navigateTo({
			url:'../search/search',
		});
	},
	//监听原生标题导航按钮点击事件
	onNavigationBarButtonTap(e) {
		//e.index为按钮的索引，如果有俩个按钮就0 ，1
		switch(e.index){
			case 0:
			break;
			case 1:
			uni.navigateTo({
				url:'../release/release',
			})
			break;
		}
	},
	methods: {
		tabtap(index){
			this.tabIndex = index
		},
		tabChange(e){
			this.tabIndex = e.detail.current;
		},
		//上拉加载事件
		loadMore(index){
			if(this.newsList[index].loadMoreText != "上拉加载更多"){ return; };
			//修改状态
			this.newsList[index].loadMoreText="加载中...";
			//获取数据
			//ES6箭头函数没有this指向，所以里面的this直接指向Vue
			setTimeout(()=>{
				//获取完成
				let obj={
						uname:"王二拿",
						uimg:"../../static/demo/1568986634772user1.jpg",
						isFocus:true,
						title:"今天是个好日子，我们都很开心。",
						type:"video",//img or video
						playnum:"23w",
						playlong:"2:47",
						infonum:{
							index:0,//0代表没操作 1顶了  2踩了
							upnum:11,
							downnum:11,
						},
						titimg:"../../static/demo/156801425158015611047757442019-6-21web3_1.jpg",
						commentnum:10,
						share:10,
					};
					this.newsList[index].list.push(obj);
				this.newsList[index].loadMoreText = "上拉加载更多"
			},1000);
			//this.newsList[index].loadMoreText="没有更多糗事了";
		},
	}
}
